@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@400;500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.box-calculator {
    background: rgb(222, 184, 135, 0.6);
    border-radius: 20px;
    padding: 28px 30px;
    box-shadow: 0 9px 14px 6px rgba(0,0,0, 0.6);
    width: 100%;
    margin: 0 auto;
}
.about-calculator {
    width: 100%;
    font-size: 28px;
    color: white;
    font-family: Georgia, 'Times New Roman', Times, serif;
    margin: 0 auto;
    margin-bottom: 25px;
    font-family: 'Roboto', sans-serif;
  }

.row-calculator {
    width: 100%;
    display: flex;
}
.label-text {
    width: 50%;
    font-size: 26px;
    color: #000;
    padding: 10px;
    font-family: 'Roboto', sans-serif;
}
.label-box {
    width: 50%;
    padding: 10px 40px;
    margin-bottom: 25px;
}
 .sum{
    font-size: 26px;
    font-weight: 500;
    ccolor: #000;
    padding: 10px;
    font-family: 'Roboto', sans-serif;
  }
.total-text {
    font-size: 26px;
    color: #000;
    padding: 10px;
    font-family: 'Roboto', sans-serif;

}@media print {

    body {
      margin: 0;
      width: 650px;
    }
    header {
      display: none;
    }
    .box-calculator {
        box-shadow: none;
        border: 1px solid gray;
        border-radius: 2px;
    }
    .row-calculator {
      display: none;
    } 
  }


.dropdown {
    max-width: 160px;
    display: none;
    flex-direction: column;
    position: absolute;
    top: 100%;
    margin-top: -28px;
    background-color: rgba(222, 184, 135);
    z-index: 1;
    padding: 0;
   
}

.dropdown li {
    font-size: 16px;
    padding: 8px 10px;
    border-bottom: 1px solid white;
    width: auto;
    position: relative;
}
.dropdown a:hover {
    color: #ffe; 
   } 

.littledropdown {
    display: none;
    font-size: 14px;
    width: auto;
    position: absolute;
    left: 100%;
    background-color: rgba(222, 184, 135);
    top: 0;
    display: none;
    flex-direction: column;
    padding: 5px;
    min-width: 200px;
}
.arrow::after {
    content: " ";
    border-top: 8px solid transparent;
    border-bottom: 8px solid transparent;
    position: absolute;
    margin-top: -8px;
    right: 2px;
    top: 50%;
    border-left: 8px solid white;
    margin-left: 6px;
}
body {
  font-family: 'Roboto', sans-serif;
  overflow-x: hidden;
}

h2 {
  margin-top: 0;
}

#root {
  height: 100vh;
}

header {
  background-color: burlywood;
  height: 80px;
  width: 100%;
  border-bottom: 2px solid white;
  position: relative;
  z-index: 2;
}

header ul {
  display: flex;
  list-style-type: none;
  margin: 0;
  justify-content: center;
}

header li {
  width: 10%;
  font-size: 24px;
  padding: 25px;
  font-weight: 700;
  position: relative;

}

header li:hover>ul {
  display: flex;

}

.logo {
  height: 90px;
  padding: 10px;
  margin-right: 80px;
  background: white;
  border-radius: 5px;
}

nav {
  position: relative;
}

header li a {
  text-decoration: none;
  color: black;
}

.enter {
  padding-top: 0;
}

.enter-door {
  background-image: url(/static/media/door-icon-png-7.7c292ae4.jpg);
  background-size: 25%;
  background-position: center;
  background-repeat: no-repeat;
  height: 100%;
}

.main-backgraund-img {
  background-image: url(/static/media/notary-public-1280x720.e58a0850.png);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

.first-screen {
  min-height: calc(100vh - 82px);
  display: flex;
  flex-direction: column;
  justify-content: center;
  position: relative;
}

h1 {
  text-align: center;
  margin: 0 0 80px;
  color: burlywood;
  letter-spacing: 5px;
  font-size: 4em;
  text-shadow: 2px 2px black;

}

.container {
  max-width: 1040px;
  margin: 0 auto;
  padding: 0 20px;
}

.overlay {
  position: relative;
  z-index: 1;
  padding: 35px;

}

.overlay::before {
  content: '';
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(80, 73, 73, 0.7);
  z-index: -1;
}

.text {
  color: rgb(172, 134, 86);
  font-size: 1.5em;
  text-align: justify;
  margin: 0 auto;
  padding-top: 30px;
}

.buttom {
  display: flex;
  justify-content: space-around;
}

.buttom a {
  width: 60%;
  padding: 10px 10px;
  text-decoration: none;
  font-size: 1.4em;
  color: rgb(172, 134, 86);
  text-shadow: 1px 1px black;
  font-weight: 600;
  margin: 0 40px;
  background-color: rgba(255, 255, 255, 0.233);
  border-radius: 15px;
  -webkit-animation: pulse 1.5s infinite;
          animation: pulse 1.5s infinite;
  text-align: center;
}

@-webkit-keyframes pulse {
  0% {
    background-color: #e4d8d8;
  }

  100% {
    background-color: #dacdcd;
  }
}

@keyframes pulse {
  0% {
    background-color: #e4d8d8;
  }

  100% {
    background-color: #dacdcd;
  }
}

.Contact-row {
  margin: 0px 10px 35px;
  width: 50%;
  font-size: 23px;
  background-color: rgb(222, 184, 135, 0.5);
  padding: 10px;
  border-radius: 5px;
}

.Contact-name {
  font-size: 22px;
  font-weight: 600;
  padding-bottom: 10px;
}

.button-down-wrapper {
  position: absolute;
  bottom: 11%;
  left: 50%;
  transform: translateX(-50px);
  height: 80px;
  -webkit-animation: move 1.5s infinite linear;
          animation: move 1.5s infinite linear;
}

.button-down {
  background-image: url(data:image/png;base64,);
  width: 100px;
  height: 60px;
  background-position: center;
  background-repeat: no-repeat;
  background-color: transparent;
  background-size: cover;
  border: transparent;
  transition: transform 0.2s linear;
  cursor: pointer;
  outline: none;

}

@-webkit-keyframes move {
  from {
    bottom: 11%;

  }

  to {
    bottom: 10%;
  }

}

@keyframes move {
  from {
    bottom: 11%;

  }

  to {
    bottom: 10%;
  }

}

.button-down:hover {
  transform: translateY(-5px);
}

.triangle {
  position: absolute;
  bottom: 0;
  left: 0;
  border-top: 80px solid transparent;
  border-left: 0px solid transparent;
  border-right: 100vw solid white;
  border-bottom: 0px solid transparent;
}

.second-screen {
  margin-top: 7%;
}

.main-height {
  box-sizing: border-box;
  min-height: 100vh;
}

